  font-size: 16px;
  overflow: hidden;
}

.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.dark {
  background-color: #111;
}

.categories-text-color-dark {
  color: #fff;
}

.color-grey {
  color: #b4afaf;
}

.category {
  width: 335px;
  height: 228px;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  overflow-x: hidden;
  overflow-y: scroll;
}

.all-category {
  margin-bottom: 20px;
  display: block;
}

.category-list {
  color: #000;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.category-link {
  color: rgba(17, 17, 17, .6);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.13;
}

.category-link:hover, .category-link:focus {
  color: #4f2ee8;
  text-transform: uppercase;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .category {
    width: 337px;
